.table {
  width: 100%;
}

.tableContainer th {
  border-left: none;
  top: 0;
  color: #2a5067;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  background-color: #e2f8fb;
  gap: 10px;
  height: 48px;
}

.tableContainer td {
  border-left: none;
  border-bottom: 1px solid #e8e9e9;
  //   padding: 14px 21px;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.tableContainer {
  // width: 678px;
  overflow: auto;
  border: 1px solid;
  border-color: #e8e9e9;
  border-radius: 16px;
}

.tableContainer table {
  border-spacing: 0;
}

// .table tbody tr td:nth-child(1),
//   .table tbody tr td:nth-child(2) {
//     padding: 14px 21px;
//   }
.tableContainer th:nth-child(1) {
  padding: 14px 21px;
}

.container {
  width: 1252px;
  margin-top: 24px;
  background-color: #e2f8fb;
  border-radius: 16px;
}

// .tableContainer td:nth-child(3) {
//   padding: 14px 8px;
//   // background: var(--accent-c-109, #F3F5E7);
// }

.dropdownArrow {
  margin: 10px;

  cursor: pointer;
}

.dropdownTable {
  background: var(--primary-10, #fafbfb);
}

.color {
  background: var(--accent-c-109, #f3f5e7);
}

.inputfield {
  border: none;
  background: var(--accent-c-109, #f3f5e7);
}

.inputBorder {
  border: none;
  outline: none;
  background: var(--primary-10, #fafbfb);
}

.table th,
td {
  border-right: 1px solid #d2d3d4;
}

.table th:last-child,
.table td:last-child {
  border-right: none;
  /* Or border-right: 0; to remove the border */
  // position: fixed;
}

.table th {
  padding: 14px 21px;
}

.tableContainer td:nth-child(2) input[type="text"] {
  border: none;
  outline: none;
  position: relative;
  top: 0;
  // height: 44px; /* Set a fixed height for the input field */
}

.font {
  color: var(--primary-01, #090909);
  /* Table/B1-Semi bold */

  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
  border: none;
  outline: none;
}

.margin {
  margin-bottom: 15px;
}

.input {
  border: none;
  outline: none;
  width: 147px;
  height: 44px;
  padding: 13px 20px;
}

.renderPaymentSelect {
  width: 100%;

  :global {
    .Select__control {
      min-height: 45px;
      min-width: 138px;
      border: 0;
      box-shadow: unset;
      padding-right: 0;
      border-radius: 0;

      &.Select__control--menu-is-open {
        border: 1px solid #245675 !important;
      }

      .Select__value-container {
        padding-left: 12px;

        .Select__placeholder {
          color: var(--primary-03-main, #1e2528);
          /* Table/B1-Regular */
          font-family: Inter;
          font-size: 12px;
          font-style: normal;
          font-weight: 400;
          line-height: 18px;
        }

        .Select__single-value {
          color: var(--primary-03-main, #1e2528);
          /* Table/B1-Regular */
          font-family: Inter;
          font-size: 12px;
          font-style: normal;
          font-weight: 400;
          line-height: 18px;
        }
      }

      .Select__indicators {
        .Select__indicator {
          padding: 0;
          padding-right: 12px;
        }

        .Select__indicator-separator {
          display: none;
        }
      }
    }
  }
}